.Movie{
  width:40%;
  display: flex;
  justify-content: space-between;
  align-items:flex-start;
  flex-wrap: wrap;
  margin-bottom: 50px;
  text-overflow: ellipsis;
  padding: 0 20px;

}

.Movie__Column{
  width:30%;
  box-sizing: border-box;
  text-overflow: ellipsis;
}

.Movie__Column:last-child{
  padding: 20px 0;width:30%;
  width: 60%;
}